@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* FONTS-------------------------------------------------------------------------------- */

@font-face {
  font-family: 'Petit Formal Script';
  src: url(/zoorit-exo/static/media/PetitFormalScript-Regular.2af3967f.woff2) format('woff2'),
      url(/zoorit-exo/static/media/PetitFormalScript-Regular.37017af6.woff) format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}


/* GENERAL-------------------------------------------------------------------------------- */
html {
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
}

/* APP------------------------------------------------------------------------------------ */
.main {
  background-image: url(/zoorit-exo/static/media/background-palmiers.1f9fea56.jpg);
  background-size: cover;
  background-position: top;
  min-height: 70vh; 
  margin-top: 1%;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.mainLine0 {
  margin-bottom: 3%;
  width:100%;
  text-align: center;
}

.mainLine1{
  margin-bottom: -150px;
  text-align: center;
}

.thumbnailsLine{
  display:flex;
  justify-content:center;
}

.bottom{
  background-color:gray;
  min-height: 30vh;
}

/* THUMBNAILs-------------------------------------------------------------------------- */
.roundThumbnail {
  width: 200px;
  height: 190px;
  background-size: cover;
  background-position: top;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 1%;
}

.roundThumbnail > h3{
  color:white;
  font-family: 'Petit Formal Script';
}


.squareThumbnail{
  margin: 0 1%;
  color: white;
}

.squareThumbnailImg {
  width: 160px;
  height: 200px;
  background-size: cover;
  background-position: top;
}

.squareThumbnailIcons{
  display: flex;
  justify-content: space-between;
  padding-top: 5%;
}

.favorite{
  display: flex;
  align-items: center;
}

.favorite > img{
  height: 20px;
  margin: 0 5px;
}

.favorite > p{
  margin: 0;
  font-size: 12px;
  font-family: 'Petit Formal Script';
}

.promo{
  background-color: black;
  border: 1px solid black;
  border-radius: 40% 0 0 40%;
  padding: 5px 2px 5px 8px;
  font-size: 10px;
  color: lightpink;
}

.cta{
  background-color: white;
  border: 1px solid black;
  border-radius: 5px;
  font-weight: bold;
}

.round{
  margin-top: 20px;
}

.square{
  margin-top: 100px;
}

.squareThumbnailTitle{
  margin-bottom: 3px;
}

.price{
  display:flex;
  justify-content: center;
}

.price > p {
  margin: 0;
}

.price > p:first-child {
  color: lightpink;
  font-weight: bold;
  margin-right: 5px;
}




